import {SafeAreaView} from 'react-native-safe-area-context';
import HeaderCM from '../../components/Header/HeaderCM';
import {TouchableOpacity, View} from 'react-native';
import ButtonCM from '../../components/Button';
import {IconRejectReview, IconWorkflow} from '../../../assets/images';
import styles from './styles';
import TabViewScreen from './tab-view';
import {useEffect, useState} from 'react';
import BottomSheetCM from '../../components/BottomSheet';
import ModalScreen from './modal';
import {SizeDP} from '../../../constants/Size';
import {useNavigation, useRoute} from '@react-navigation/native';
import {useAppSelector} from '../../../hooks/useRedux';
import {IPostMessageUserTask, STATUS_TASK} from './interface';
import {IFormByUserTaskId} from './tab-view/components/BackgroundFlow/interface';
import {IRecipientTicketImpl} from '../../../model/interface/service.interface';
import NavigationName from '../../../constants/NavigationName';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import {Divider, TopNavigationAction} from '@ui-kitten/components';
import ModalFullScreen from '../../components/ModalFullScreen';
import StepCM from '../review-eid/components/Step';
const ReviewFlow = () => {
  const router = useRoute<any>();
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const dataUserTask: IPostMessageUserTask = router?.params?.dataUserTask ?? {};
  const [openSubmitBottomSheet, setOpenSubmitBottomSheet] = useState(false);
  const [openRejectBottomSheet, setOpenRejectBottomSheet] = useState(false);
  const [dataFormByUsertaskId, setDataFormByUsertaskId] =
    useState<IFormByUserTaskId>();
  const [dataRecipientImpl, setDataRecipientImpl] =
    useState<IRecipientTicketImpl>();
  const [isShow, setisShow] = useState(false);
  const {accessToken} = useAppSelector(state => state.common);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);

  const ReviewTicketServices = useReviewServices();

  useEffect(() => {
    getDataFormByUserTaskId();
    getRecipientImpl();
  }, []);

  const getRecipientImpl = async () => {
    try {
      const dataRecipientImpl =
        (await ReviewTicketServices.getRecipientDetaiImpl(
          dataTicketRedux.id,
          accessToken,
        )) as IRecipientTicketImpl[];
      setDataRecipientImpl(
        dataRecipientImpl.find(
          data => data.userTaskId === dataUserTask._userTaskId,
        ),
      );
    } catch (error) {}
  };

  const getDataFormByUserTaskId = async () => {
    try {
      const dataFormByUsertaskId =
        await ReviewTicketServices.getFromByUserTaskId(
          dataUserTask._userTaskId,
          accessToken,
        );
      setDataFormByUsertaskId(dataFormByUsertaskId?.data ?? {});
    } catch (error) {}
  };

  const handleSubmit = async (version: any) => {
    const body = {
      userTaskId: dataUserTask._userTaskId,
      ticketId: dataTicketRedux.id,
      version: version,
    };

    const data = await ReviewTicketServices.getFormVersionByTicketAndUserTaskId(
      body,
      accessToken,
    );
    if (dataFormByUsertaskId?.versionId && !data) {
      navigation.navigate(NavigationName.ViewFormScreen, {
        versionId: dataFormByUsertaskId.versionId,
        readOnly: dataUserTask._status === STATUS_TASK.TASK_SUCCESS,
        isShowNoti: true,
        data: {
          _userTaskId: dataUserTask._userTaskId,
          _ticketId: dataTicketRedux.id,
          formId: dataFormByUsertaskId.formId,
        },
      });
      return;
    }
    setOpenSubmitBottomSheet(true);
  };

  const renderRightAction = () => {
    return (
      <TopNavigationAction
        onPress={() => setisShow(true)}
        icon={<IconWorkflow width={20} height={20} />}
      />
    );
  };

  return (
    <SafeAreaView edges={['bottom']} style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        title={t('RV.infoDoc')}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        renderContentRight={renderRightAction}
      />
      <Divider />
      <View style={styles.ctnBody}>
        {dataFormByUsertaskId && dataRecipientImpl && (
          <TabViewScreen
            dataFormByUsertaskId={dataFormByUsertaskId}
            dataRecipientImpl={dataRecipientImpl}
            dataUserTask={dataUserTask}
          />
        )}
      </View>
      <View style={styles.ctnFooter}>
        {dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
          <>
            <TouchableOpacity onPress={() => setOpenRejectBottomSheet(true)}>
              <IconRejectReview width={SizeDP(36)} height={SizeDP(36)} />
            </TouchableOpacity>
            <ButtonCM
              style={{flex: 1}}
              label={t('CM.confirm')}
              onPress={() => handleSubmit(null)}
            />
          </>
        )}
      </View>

      {dataRecipientImpl && (
        <>
          <BottomSheetCM
            title={t('CM.confirm')}
            isVisible={openSubmitBottomSheet}
            onClose={() => setOpenSubmitBottomSheet(false)}
            withKeyboardAvoidingView={true}
            renderContent={
              <ModalScreen
                dataRecipientImpl={dataRecipientImpl}
                onClose={() => setOpenSubmitBottomSheet(false)}
              />
            }
          />

          <BottomSheetCM
            title={t('RV.reject')}
            isVisible={openRejectBottomSheet}
            onClose={() => setOpenRejectBottomSheet(false)}
            withKeyboardAvoidingView={true}
            renderContent={
              <ModalScreen
                dataRecipientImpl={dataRecipientImpl}
                onClose={() => setOpenRejectBottomSheet(false)}
                type="reject"
              />
            }
          />
        </>
      )}

      <ModalFullScreen
        isVisible={isShow}
        children={<StepCM onClose={() => setisShow(false)} />}
      />
    </SafeAreaView>
  );
};

export default ReviewFlow;
