import {Image, TouchableOpacity, View} from 'react-native';
import {IconTrash} from '../../../../../assets/images';
import styles from './styles';
import TextCM from '../../../../components/Text';
import {SizeDP} from '../../../../../constants/Size';
import {Color, String} from '../../../../../constants';
import {Status} from '../../../../../model/enum/common.enum';
import {ITicket} from '../../../../../model/interface/service.interface';
import {FC} from 'react';
import {DateUtils} from '../../../../../utils';
import {IAccount} from '../../../../../model/interface/account.interface';
import {useTranslation} from 'react-i18next';

type Props = {
  dataCard: ITicket;
  onSelect: (listSelect: ITicket[]) => void;
  select: ITicket[];
  account: IAccount;
};

const CardTicketRelative: FC<Props> = ({
  dataCard,
  onSelect,
  select,
  account,
}) => {
  const {t} = useTranslation();
  const handleClearData = () => {
    const filterData = select.filter(item => item.id !== dataCard.id);
    onSelect(filterData);
  };

  const renderContent = (color: string, bgColor: string, status: Status) => {
    return (
      <View
        style={{
          backgroundColor: bgColor,
          paddingHorizontal: SizeDP(10),
          paddingVertical: SizeDP(6),
          borderRadius: SizeDP(8),
          alignItems: 'center',
          justifyContent: 'center',
          marginTop: SizeDP(6),
          maxWidth: 160,
        }}>
        <TextCM style={{color: color}}>{t(`TK.${status}`)}</TextCM>
      </View>
    );
  };

  const renderStatusTicket = () => {
    switch (dataCard?.ticketStatus) {
      case Status.completed:
        return renderContent(
          Color.completed,
          Color.completed01,
          Status.completed,
        );
      case Status.draft:
        return renderContent(Color.draft, Color.draft01, Status.draft);
      case Status.voided:
        return renderContent(Color.voided, Color.voided01, Status.voided);
      case Status.rejected:
        return renderContent(Color.rejected, Color.rejected01, Status.rejected);
      case Status.processing:
        return renderContent(
          Color.processing,
          Color.processing01,
          Status.processing,
        );
    }
  };

  return (
    <View style={styles.ctnMain}>
      <View style={{rowGap: SizeDP(6)}}>
        <TextCM>{dataCard?.title}</TextCM>
        {/* <TextCM style={styles.subText}>Nhn s </TextCM> */}
        <View style={styles.ctnInfo}>
          <TextCM>
            {DateUtils.convertDateToDDMMYYYY_H_M(dataCard?.createdDate)}
          </TextCM>

          {account?.avatar ? (
            <View style={styles.ctnImage}>
              <Image
                width={SizeDP(16)}
                height={SizeDP(16)}
                style={{borderRadius: SizeDP(8)}}
                source={{uri: String.PREFIX_IMG_BASE64 + account?.avatar}}
              />
            </View>
          ) : (
            <View style={styles.ctnImage}></View>
          )}
          <TextCM numberOfLines={1} style={{width: SizeDP(150)}}>
            {dataCard?.createdBy}
          </TextCM>
        </View>
        {renderStatusTicket()}
      </View>
      <TouchableOpacity onPress={handleClearData}>
        <IconTrash width={SizeDP(16)} height={SizeDP(16)} />
      </TouchableOpacity>
    </View>
  );
};

export default CardTicketRelative;
