import {View} from 'react-native';
import ButtonCM from '../../../components/Button';
import styles from './styles';
import TextFieldCM from '../../../components/TextField';
import {SafeAreaView} from 'react-native-safe-area-context';
import {SizeDP} from '../../../../constants/Size';
import {useEffect, useState} from 'react';
import {isEmpty} from 'lodash';
import {useAppDispatch, useAppSelector} from '../../../../hooks/useRedux';
import {IRecipientTicketImpl} from '../../../../model/interface/service.interface';
import Toast from 'react-native-toast-message';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../hooks/apiHooks/useReviewTicketServices';

type Props = {
  type?: 'submit' | 'reject';
  onClose: () => void;
  dataRecipientImpl: IRecipientTicketImpl;
};

const ModalScreen = ({type = 'submit', onClose, dataRecipientImpl}: Props) => {
  const dispatch = useAppDispatch();
  const {t} = useTranslation();
  const navigation = useNavigation();
  const [errorMessage, setErrorMessage] = useState('');
  const [valueErrorMessage, setValueErrorMessage] = useState('');
  const [valueMessage, setValueMessage] = useState('');
  const {accessToken} = useAppSelector(state => state.common);
  const ReviewTicketServices = useReviewServices();
  const handleSubmit = async () => {
    try {
      if (type === 'reject') {
        const body = {
          ...dataRecipientImpl.recipient,
          reason: valueErrorMessage,
        };
        const dataReject = await ReviewTicketServices.actionTicket(
          dataRecipientImpl.id,
          dataRecipientImpl.ticketId,
          dataRecipientImpl.userTaskId,
          body,
          accessToken,
          'reject',
        );
        if (dataReject) {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: 'T chi yu cu thnh cng',
          });
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: 'T chi yu cu tht bi',
          });
        }
      } else {
        const body = {
          ...dataRecipientImpl.recipient,
          reason: valueMessage,
          risk: valueMessage,
        };
        const dataFinish = await ReviewTicketServices.actionTicket(
          dataRecipientImpl.id,
          dataRecipientImpl.ticketId,
          dataRecipientImpl.userTaskId,
          body,
          accessToken,
          'finish',
        );
        if (dataFinish) {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: 'Xt duyt thnh cng',
          });
          onClose();
          navigation.goBack();
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: 'Xt duyt tht bi',
          });
        }
      }
    } catch (error) {
      console.log(error);

      if (type === 'reject') {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: 'T chi yu cu tht bi',
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: 'Xt duyt tht bi',
        });
      }
    }
  };

  useEffect(() => {
    if (isEmpty(valueErrorMessage)) {
      setErrorMessage('Yu cu nhp l do t chi');
    } else {
      setErrorMessage('');
    }
  }, [valueErrorMessage]);

  return (
    <SafeAreaView edges={['bottom']}>
      {type === 'reject' && (
        <TextFieldCM
          isRequired={true}
          multiline
          isFloatingLabel={true}
          label="Nhp l do t chi"
          maxLength={400}
          errorMessage={errorMessage}
          value={valueErrorMessage}
          onChangeText={setValueErrorMessage}
        />
      )}

      {type === 'submit' && (
        <View style={{rowGap: SizeDP(10)}}>
          <TextFieldCM
            value={valueMessage}
            onChangeText={setValueMessage}
            multiline
            label="Nhp ghi ch"
            maxLength={400}
          />
        </View>
      )}
      <View style={styles.ctnFooter}>
        <ButtonCM
          styleLabel={styles.labelBack}
          style={styles.btnBack}
          label="Quay li"
          onPress={onClose}
        />
        <ButtonCM
          style={[styles.btnNext, type === 'reject' && styles.btnReject]}
          label="Xc nhn"
          onPress={handleSubmit}
        />
      </View>
    </SafeAreaView>
  );
};

export default ModalScreen;
