import {useState} from 'react';
import {FlatList, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../components/Text';
import styles from './styles';
import InfoReview from './components/BackgroundFlow';
import Document from './components/Document';
import {IRecipientTicketImpl} from '../../../../model/interface/service.interface';
import {IPostMessageUserTask} from '../interface';
import {IFormByUserTaskId} from './components/BackgroundFlow/interface';
import {useTranslation} from 'react-i18next';
const TabViewScreen = ({
  dataUserTask,
  dataFormByUsertaskId,
  dataRecipientImpl,
}: {
  dataUserTask: IPostMessageUserTask;
  dataFormByUsertaskId: IFormByUserTaskId;
  dataRecipientImpl: IRecipientTicketImpl;
}) => {
  const {t} = useTranslation();
  const [index, setIndex] = useState<any>({
    key: 'background',
    title: 'Thng tin chung',
  });
  const [routes] = useState([
    {key: 'background', title: 'Thng tin chung'},
    {key: 'document', title: 'Ti liu nh km'},
  ]);

  const renderTabView = ({item}: {item: any}) => {
    return (
      <TouchableOpacity
        style={[styles.ctnMainTab, index.key === item.key && styles.activeTab]}
        onPress={() => setIndex(item)}>
        <TextCM
          style={[styles.tabBar, index.key === item.key && styles.colorActive]}>
          {t(`RV.${item.key}`)}
        </TextCM>
      </TouchableOpacity>
    );
  };

  const renderItems = (item: any) => {
    switch (item.key) {
      case 'background':
        return (
          <InfoReview
            dataFormByUsertaskId={dataFormByUsertaskId}
            dataUserTask={dataUserTask}
            dataRecipientImpl={dataRecipientImpl}
          />
        );
      case 'document':
        return <Document dataUserTask={dataUserTask} />;
      default:
        return <View />;
    }
  };
  return (
    <>
      <View>
        <FlatList
          horizontal
          scrollEnabled={false}
          data={routes}
          keyExtractor={(item, index) => String(item.key)}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          contentContainerStyle={styles.ctnTabView}
          renderItem={renderTabView}
        />
      </View>
      {renderItems(index)}
    </>
  );
};

export default TabViewScreen;
