import {
  ActivityIndicator,
  Dimensions,
  FlatList,
  useWindowDimensions,
  View,
} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import TextCM from '../../../../components/Text';
import React, {FC, useEffect, useState} from 'react';
import styles from './styles';
import KanBanItem from '../KanBanItem';
import {ITicket} from '../../../../../model/interface/service.interface';
import {Color} from '../../../../../constants';
import {useAppSelector} from '../../../../../hooks/useRedux';
import Toast from 'react-native-toast-message';
import {IListStatusTicket} from '../../../my-ticket/helper';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
type Props = {
  dataListStatus: IListStatusTicket;
};

const KanBan: FC<Props> = ({dataListStatus}) => {
  const {t} = useTranslation();
  const [listDataTicket, setListDataTicket] = useState<ITicket[]>([]);
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);

  const {accessToken} = useAppSelector(state => state.common);
  const ListServices = useTicketServices();
  const {width} = useWindowDimensions();

  useEffect(() => {
    getListTicket(page);
  }, [page]);

  const getListTicket = async (page: number) => {
    try {
      const search = {
        keyWord: '',
        terms: {status: dataListStatus.status},
        id: null,
      };

      const data = await ListServices.getTicketListFollow(
        page,
        20,
        'createdDate,desc',
        search,
        accessToken,
      );

      if (page === 0) {
        setListDataTicket(data?.content);
      } else {
        setListDataTicket([...listDataTicket, ...data?.content]);
      }
      setTotalRecoders(Number(data?.page?.totalRecords));
    } catch (error) {
    } finally {
      setReloading(false);
    }
  };

  const fetchNextPage = () => {
    if (listDataTicket.length < totalRecoders) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setReloading(true);
      setPage(0);
    } else {
      getListTicket(0);
    }
  };

  const handleCancelTicket = async (item: ITicket) => {
    try {
      const params = {
        ticketId: item?.resourceId?.split('sid_')[1] ?? '',
        reason: '',
      };
      const data = await ListServices.cancelTicket(params, accessToken);
      if (data) {
        Toast.show({
          text1: t('CM.noti'),
          type: 'success',
          text2: t('TK.rejectSuccess'),
        });
        setPage(0);
      } else {
        Toast.show({
          text1: t('CM.noti'),
          type: 'error',
          text2: t('TK.rejectFailure'),
        });
      }
    } catch (error) {}
  };

  return (
    <>
      <SafeAreaView
        style={[
          styles.ctnMain,
          {
            width: width - 40,
          },
        ]}
        edges={['left', 'right']}>
        <View style={styles.ctnHeader}>
          <View
            style={[styles.ctnNumber, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>{totalRecoders}</TextCM>
          </View>
          <View
            style={[styles.ctnText, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>
              {t(`TK.name_${dataListStatus.status}`)}
            </TextCM>
          </View>
        </View>
        {listDataTicket ? (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listDataTicket}
            refreshing={reloading}
            onRefresh={onRefresh}
            onEndReached={fetchNextPage}
            onEndReachedThreshold={0.5}
            keyExtractor={item => '' + item.id}
            renderItem={({item}) => (
              <KanBanItem
                item={item}
                dataListStatus={dataListStatus}
                handleCancelTicket={handleCancelTicket}
              />
            )}
          />
        ) : (
          <View
            style={{alignItems: 'center', justifyContent: 'center', flex: 1}}>
            <ActivityIndicator size={'large'} color={Color.BgPrimary} />
          </View>
        )}
      </SafeAreaView>
    </>
  );
};

export default KanBan;
