//import liraries
import React, {useEffect, useState} from 'react';
import {Image, ScrollView, TouchableOpacity, View} from 'react-native';
import FontSize, {SizeDP} from '../../../../../../constants/Size';
import TextCM from '../../../../../components/Text';
import styles from './styles';
import {IRecipientTicketImpl} from '../../../../../../model/interface/service.interface';
import {useAppSelector} from '../../../../../../hooks/useRedux';
import {IFormByUserTaskId, IFormReview, ITask} from './interface';
import {IModeler} from '../../../../../../model/interface/modeler.interface';
import {IPostMessageUserTask, STATUS_TASK} from '../../../interface';
import {useTranslation} from 'react-i18next';
import {useNavigation} from '@react-navigation/native';
import {NavigationName} from '../../../../../../constants';
import {useTicketServices} from '../../../../../../hooks/apiHooks/useTicketServices';
import {useReviewServices} from '../../../../../../hooks/apiHooks/useReviewTicketServices';
type Props = {
  dataUserTask: IPostMessageUserTask;
  dataFormByUsertaskId: IFormByUserTaskId;
  dataRecipientImpl: IRecipientTicketImpl;
};

// create a component
const InfoReview = ({
  dataUserTask,
  dataFormByUsertaskId,
  dataRecipientImpl,
}: Props) => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {accessToken} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const [dataTicketRaw, setDataTicketRaw] = useState<any>();
  const [dataForm, setDataForm] = useState<IFormReview>();
  const [dataFormInfo, setDataFormInfo] = useState<IFormReview>();
  const [dataFormByUsertaskIdState, setDataFormByUsertaskIdState] =
    useState<IFormReview>();
  const [dataModeler, setDataModeler] = useState<IModeler>();
  const [listTask, setListTask] = useState<ITask[]>([]);
  const ListServices = useTicketServices();
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    Promise.all([
      getDataForm(),
      getDataTicketRaw(),
      getModelerById(),
      getDataFormInfo(),
      getListTask(),
    ]);
  }, []);

  const getListTask = async () => {
    const dataResp =
      await ReviewTicketServices.getListTaskByTicketIdAndUsertaskId(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
      );
    if (dataResp) {
      setListTask(dataResp);
    }
  };

  const getDataTicketRaw = async () => {
    try {
      const data = await ReviewTicketServices.getStatusParty(
        dataTicketRedux?.id,
        accessToken,
      );
      setDataTicketRaw(data);
    } catch (error) {}
  };

  const getModelerById = async () => {
    try {
      const data = await ListServices.getModeler(
        dataTicketRedux.modelerId,
        accessToken,
      );
      setDataModeler(data);
    } catch (error) {}
  };

  const getDataForm = async () => {
    try {
      const params = {
        versionId: dataFormByUsertaskId?.versionId,
      };
      const data = await ReviewTicketServices.getFormERequest(
        params,
        accessToken,
      );
      setDataForm(data);
    } catch (error) {}
  };

  const getDataFormInfo = async () => {
    try {
      const params1 = {
        modelerId: dataTicketRedux.modelerId,
        attachedUserTaskId: dataUserTask._userTaskId,
      };
      const dataForm = await ReviewTicketServices.getFormDataByUserTaskId(
        params1,
        accessToken,
      );
      if (dataForm) {
        setDataFormByUsertaskIdState(dataForm?.data);
        const params = {
          versionId: dataForm?.data?.versionId,
        };
        const data = await ReviewTicketServices.getFormERequest(
          params,
          accessToken,
        );
        setDataFormInfo(data);
      }
    } catch (error) {}
  };

  const renderStatus = () => {
    return (
      <View style={styles.ctnStatus}>
        <TextCM
          style={[
            styles.txtValue,
            {
              color: '#EF5DA8',
            },
          ]}>
          {t(`TK.${dataUserTask?._status}`)}
        </TextCM>
      </View>
    );
  };

  const handleViewForm = (view: boolean) => {
    console.log('dataUserTask', dataUserTask);

    if (!dataForm && !view) return;
    if (!dataFormInfo && view) return;
    navigation.navigate(NavigationName.ViewFormScreen, {
      versionId: !view
        ? dataFormByUsertaskId.versionId
        : dataFormByUsertaskIdState?.versionId,
      readOnly: dataUserTask._status !== STATUS_TASK.TASK_SEARCH,
      data: {
        _userTaskId: dataUserTask._userTaskId,
        _ticketId: dataTicketRedux.id,
        formId: !view
          ? dataFormByUsertaskId.formId
          : dataFormByUsertaskIdState?.formId,
      },
      isOnlyView: view,
    });
  };

  return (
    <View style={styles.ctnMain}>
      <ScrollView style={styles.ctnBody}>
        <View style={styles.ctnWrapperItem}>
          <TextCM style={styles.txtTitle}>{t('RV.nameJob')}:</TextCM>
          <TextCM style={styles.txtValue}>
            {dataTicketRaw?.ticketName ?? '---'}
          </TextCM>
        </View>

        <View style={styles.ctnWrapperItem}>
          <TextCM style={styles.txtTitle}>{t('RV.nameService')}:</TextCM>
          <TextCM style={styles.txtValue}>{dataModeler?.name ?? '---'}</TextCM>
        </View>
        {dataForm && (
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>{t('RV.form')}:</TextCM>
            <TouchableOpacity
              style={{flex: 1}}
              onPress={() => handleViewForm(false)}>
              {dataForm ? (
                <>
                  <TextCM style={[styles.txtValue, styles.formName]}>
                    {dataForm?.formName ?? '---'}
                  </TextCM>
                  <TextCM
                    style={{
                      fontSize: FontSize.FontTinier,
                      fontStyle: 'italic',
                    }}>
                    {t('RV.viewDetail')}
                  </TextCM>
                </>
              ) : (
                <TextCM style={styles.txtValue}>{'---'}</TextCM>
              )}
            </TouchableOpacity>
          </View>
        )}
        {dataFormInfo && (
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>{t('RV.formView')}:</TextCM>
            <TouchableOpacity
              style={{flex: 1}}
              onPress={() => handleViewForm(true)}>
              {dataFormInfo ? (
                <>
                  <TextCM style={[styles.txtValue, styles.formName]}>
                    {dataFormInfo?.formName ?? '---'}
                  </TextCM>
                  <TextCM
                    style={{
                      fontSize: FontSize.FontTinier,
                      fontStyle: 'italic',
                    }}>
                    {t('RV.fromViewDetail')}
                  </TextCM>
                </>
              ) : (
                <TextCM style={styles.txtValue}>{'---'}</TextCM>
              )}
            </TouchableOpacity>
          </View>
        )}
        <View style={styles.ctnWrapperItem}>
          <TextCM style={styles.txtTitle}>{t('RV.userAction')}:</TextCM>
          <View style={styles.ctnView}>
            <Image
              width={SizeDP(24)}
              height={SizeDP(24)}
              borderRadius={SizeDP(12)}
              source={{
                uri: dataAccount?.imageUrl
                  ? dataAccount?.imageUrl
                  : 'https://static.vecteezy.com/system/resources/previews/009/292/244/original/default-avatar-icon-of-social-media-user-vector.jpg',
              }}
            />
            <View style={{flex: 1}}>
              <TextCM style={styles.txtValue}>
                {'' + dataAccount?.firstName + ' ' + dataAccount?.lastName ||
                  '---'}
              </TextCM>
              <TextCM style={styles.txtValue}>
                {dataAccount?.email ?? '---'}
              </TextCM>
            </View>
          </View>
        </View>
        {listTask.length > 0 && (
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>{t('RV.jobNeedAction')}:</TextCM>
            <TextCM style={styles.txtValue}>{'---'}</TextCM>
          </View>
        )}
        {dataUserTask._status === STATUS_TASK.TASK_REJECTED && (
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>{t('RV.reason')}:</TextCM>
            <TextCM style={styles.txtValue}>
              {dataRecipientImpl?.recipient?.reason || '---'}
            </TextCM>
          </View>
        )}
        {dataUserTask._status === STATUS_TASK.TASK_SUCCESS && (
          <View style={styles.ctnWrapperItem}>
            <TextCM style={styles.txtTitle}>{t('RV.submit')}:</TextCM>
            <TextCM style={styles.txtValue}>
              {dataUserTask?.reason || '---'}
            </TextCM>
          </View>
        )}
        <View
          style={[
            styles.ctnWrapperItem,
            {
              justifyContent: 'flex-start',
              columnGap: 0,
            },
          ]}>
          <TextCM style={styles.txtTitle}>{t('TK.status')}:</TextCM>
          {renderStatus()}
        </View>
      </ScrollView>
    </View>
  );
};

//make this component available to the app
export default InfoReview;
