import {
  ActivityIndicator,
  Dimensions,
  FlatList,
  useWindowDimensions,
  View,
} from 'react-native';
import {SafeAreaView} from 'react-native-safe-area-context';
import TextCM from '../../../../components/Text';
import React, {FC, useEffect, useState} from 'react';
import {IListStatusTicket} from '../../helper';
import styles from './styles';
import KanBanItem from '../KanBanItem';
import {ITicket} from '../../../../../model/interface/service.interface';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {Color} from '../../../../../constants';
import Toast from 'react-native-toast-message';
import {Status} from '../../../../../model/enum/common.enum';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import BottomSheetCM from '../../../../components/BottomSheet';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import TextFieldCM from '../../../../components/TextField';
type Props = {
  dataListStatus: IListStatusTicket;
};

const KanBan: FC<Props> = ({dataListStatus}) => {
  const {t} = useTranslation();
  const [listDataTicket, setListDataTicket] = useState<ITicket[]>([]);
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);
  const [bottomSheetVisible, setBottomSheetVisible] = useState(false);
  const [reason, setReason] = useState('');
  const [error, setError] = useState('');
  const [ticketSelect, setTicketSelect] = useState<ITicket>();

  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);
  const {width} = useWindowDimensions();
  const ListServices = useTicketServices();

  const toogleBottomSheet = () => setBottomSheetVisible(!bottomSheetVisible);

  useEffect(() => {
    getListTicket(page);
  }, [page]);

  const getListTicket = async (page: number) => {
    try {
      const bodySubmit = {
        createdBy: dataAccount?.email ?? 'elmstest7@gmail.com',
        myTicket: true,
        textSearch: '',
        ticketStatus: [dataListStatus.status],
      };
      const data = await ListServices.getTicketList(
        page,
        20,
        dataListStatus.status === Status.processing
          ? 'createdDate,desc'
          : 'lastModifiedDate,desc',
        accessToken,
        bodySubmit,
      );

      if (page === 0) {
        setListDataTicket(data?.content);
      } else {
        setListDataTicket([...listDataTicket, ...data?.content]);
      }
      setTotalRecoders(Number(data?.page?.totalRecords));
    } catch (error) {
    } finally {
      setReloading(false);
    }
  };

  const fetchNextPage = () => {
    if (listDataTicket.length < totalRecoders) {
      setPage(page + 1);
    }
  };

  const onRefresh = () => {
    if (page !== 0) {
      setReloading(true);
      setPage(0);
    } else {
      getListTicket(0);
    }
  };

  const handleCancelTicket = async (reason: string) => {
    if (reason?.length === 0) {
      setError('Yu cu nhp l do hy');
      return;
    }
    try {
      const params = {
        ticketId: ticketSelect?.id,
        reason: reason,
      };
      const data = await ListServices.cancelTicket(params, accessToken);
      if (data) {
        Toast.show({
          text1: t('CM.noti'),
          type: 'success',
          text2: t('TK.rejectSuccess'),
        });
        setPage(0);
      } else {
        Toast.show({
          text1: t('CM.noti'),
          type: 'error',
          text2: t('TK.rejectFailure'),
        });
      }
    } catch (error) {}
  };

  return (
    <>
      <SafeAreaView
        style={[
          styles.ctnMain,
          {
            width: width - 40,
          },
        ]}
        edges={['left', 'right']}>
        <View style={styles.ctnHeader}>
          <View
            style={[styles.ctnNumber, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>{totalRecoders}</TextCM>
          </View>
          <View
            style={[styles.ctnText, {backgroundColor: dataListStatus.color}]}>
            <TextCM style={styles.text}>
              {t(`TK.name_${dataListStatus.status}`)}
            </TextCM>
          </View>
        </View>
        {listDataTicket ? (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listDataTicket}
            refreshing={reloading}
            onRefresh={onRefresh}
            onEndReached={fetchNextPage}
            onEndReachedThreshold={0.5}
            keyExtractor={item => '' + item.id}
            contentContainerStyle={{backgroundColor: '#fff'}}
            renderItem={({item}) => (
              <KanBanItem
                item={item}
                dataListStatus={dataListStatus}
                setTicketSelect={setTicketSelect}
                toggleModalCancel={toogleBottomSheet}
              />
            )}
          />
        ) : (
          <View
            style={{alignItems: 'center', justifyContent: 'center', flex: 1}}>
            <ActivityIndicator size={'large'} color={Color.BgPrimary} />
          </View>
        )}
      </SafeAreaView>

      {bottomSheetVisible && (
        <ModalConfirmCM
          isVisible={bottomSheetVisible}
          onClose={toogleBottomSheet}
          title={t('Xc nhn hy giao dch')}
          type="cancel"
          labelCancel={t('HM.cancel')}
          labelConfirm={t('CM.labelCancel')}
          styleBtnRight={{backgroundColor: Color.RedE14337}}
          onConfirm={() => handleCancelTicket(reason)}
          renderContent={() => (
            <View style={{width: '100%', marginVertical: 6}}>
              <TextFieldCM
                value={reason}
                onChangeText={setReason}
                errorMessage={error}
                isRequired={true}
                multiline
                isFloatingLabel={true}
                label="Nhp l do hy"
                maxLength={400}
              />
            </View>
          )}
        />
      )}
    </>
  );
};

export default KanBan;
